const scopesOrderedByLabel = Object.values(datasets)
  .sort((a, b) => a.label.localeCompare(b.label))
  .map(({ id }) => id)

const chatDatasets: DatasetCollection = scopesOrderedByLabel.reduce((acc, scope) => {
  return { ...acc, [scope]: { ...datasets[scope] } }
}, {} as DatasetCollection)

const promptFilterChatDatasets = Object.fromEntries(
  Object.entries(chatDatasets).filter(([_, dataset]) => dataset.isPromptFilter),
)

const isDatasetScope = (value: string | undefined): value is DatasetScope => {
  return value !== undefined && datasets[value as DatasetScope] !== undefined
}

const canDownloadCSV = (scope: DatasetScope | undefined) => {
  const allowedScopes: Array<DatasetScope> = ["job-analytics", "sentiment-attention"]
  if (scope && allowedScopes.includes(scope)) {
    return true
  }
  return false
}

export {
  canDownloadCSV,
  chatDatasets,
  isDatasetScope,
  promptFilterChatDatasets,
  scopesOrderedByLabel,
}
